.container {
  width: 100%;
  height: 100%;
  .select {
    display: flex;
    flex-direction: row;
    select {
      margin-left: 10px;
    }
  }
  .submit {
    cursor: pointer;
    position: absolute;
    bottom: 20px;
    right: 20px;
    padding: 20px 30px;
    border-radius: 10px;
    border: none;
    color: white;
    background-color: var(--active);
    &:hover {
      background-color: var(--active-hover);
    }
  }
  .back {
    cursor: pointer;
    position: absolute;
    bottom: 30px;
    left: 30px;
    padding: 20px 30px;
    border-radius: 10px;
    border: none;
    color: white;
    background-color: var(--deactive);
    &:hover {
      background-color: var(--deactive-hover);
    }
  }
}
